import React, { useState, useEffect } from "react";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CRow,
  CAlert,
} from "@coreui/react";
import axios from "axios";
import { useSelector } from "react-redux";
import { selectUser } from "src/localyStore";
import { Add, Clear } from "@material-ui/icons";
import { ImageHandler } from "src/reusable/imageHandeller";
const server = process.env.REACT_APP_SERVER;

const Offers = () => {
  const [offers, setOffers] = useState([]);
  const [products, setProducts] = useState(null);
  const [flashSales, setFlashSale] = useState();
  const [reload, setReload] = useState(false);
  const [activeOffer, setActiveOffer] = useState();
  const [offerIndex, setOfferIndex] = useState(0);
  const { token } = useSelector(selectUser);

  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  useEffect(() => {
    axios
      .get(server + process.env.REACT_APP_ADMIN_GET_FLASH_SALE, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        setFlashSale(res.data.flashSales[0]);
        if (offerIndex === 5) {
          changeData(
            JSON.stringify({
              ...res.data.flashSales[0],
              index: 5,
              offerProducts: res.data.flashSales[0]?.flashsaleProducts,
            })
          );
        }
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
      });
    axios
      .get(server + process.env.REACT_APP_ADMIN_GET_OFFERS, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        setOffers(res.data.offers);
        if (offerIndex !== 5) {
          changeData(
            JSON.stringify({
              ...res.data.offers[offerIndex],
              index: offerIndex,
            })
          );
        }
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
      });
  }, [token, reload]);
  // change data to custom
  const changeData = (item) => {
    let newItem = [];
    item = JSON.parse(item);
    setActiveOffer({
      banner: item.banner,
      offerId: item._id,
      offerName: item.flashSalesTitle ? "flash-sales" : "offer",
    });
    item?.offerProducts.forEach((product) => {
      newItem.push({
        _id: product.product._id,
        name: product.product.name,
        quantity: product.product.quantity,
        productPictures: product.product.productPictures,
        finalPrice: product.product.finalPrice,
        discount: product.discount,
        price: product.product.price,
      });
    });
    setProducts(newItem);
    setOfferIndex(item.index);
  };
  // delete product by id
  const deleteProduct = (item) => {
    axios
      .delete(server + process.env.REACT_APP_ADMIN_DELETE_OFFERS + item._id, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        setReload(!reload);
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  // image change
  const replaceImage = async (type, img, deleteId) => {
    let result;
    if (type === "add") {
      result = await ImageHandler(
        activeOffer?.offerId,
        activeOffer?.offerName,
        null,
        img,
        null,
        token,
        null
      );
    } else if (type === "delete") {
      result = await ImageHandler(
        activeOffer.offerId,
        activeOffer.offerName,
        null,
        null,
        deleteId,
        token,
        null
      );
    } else {
      result = await ImageHandler(
        activeOffer.offerId,
        activeOffer.offerName,
        img,
        null,
        token,
        null
      );
    }
    if (result) {
      setReload(!reload);
      setEvent({
        type: "success",
        massage: "images canged",
      });
      clearEvent(5000);
    } else {
      setEvent({
        type: "error",
        massage: "img size not be grater than 10mb",
      });
      clearEvent(5000);
    }
    return;
  };
  return (
    <CRow>
      <div className="d-flex align-items-center">
        {activeOffer?.banner?.map((item) => (
          <div
            className="mx-3 mb-4 d-flex align-items-center justify-content-center"
            style={{
              position: "relative",
            }}
            key={item._id}
          >
            <a href={item.img} target="_blank" rel="noreferrer">
              <img
                alt="offers"
                width="200"
                height="100"
                src={item.img}
                alt="icon"
                loading="lazy"
              />
            </a>
            <div
              style={{ position: "absolute", top: 0, right: -4 }}
              onClick={() => replaceImage("delete", null, item.keys)}
            >
              <div className="p-1 bg-danger text-white rounded-pill">
                <Clear fontSize="small" />
              </div>
            </div>
          </div>
        ))}
        <div>
          <CButton
            className="flex align-items-center justify-content-center mx-4 pt-2 px-2"
            color="info"
            size="sm"
            shape="pill"
          >
            <label htmlFor="offerBanner">
              <Add fontSize="medium" />
            </label>
          </CButton>
          <input
            accept="image/png, image/jpeg,image/jpg,image/svg,image/webp"
            type="file"
            id="offerBanner"
            hidden
            onChange={(e) => replaceImage("add", e.target.files[0], null)}
          />
        </div>
      </div>
      <CCol xl={12}>
        <CCard>
          <CCardHeader>
            <select
              style={{ height: "30px" }}
              defaultValue={JSON.stringify({
                ...offers[offerIndex],
                index: offerIndex,
              })}
              onChange={(e) => changeData(e.target.value)}
            >
              {offers && (
                <option
                  value={JSON.stringify({
                    ...flashSales,
                    index: 5,
                    offerProducts: flashSales?.flashsaleProducts,
                  })}
                >
                  {flashSales?.flashSalesTitle}
                </option>
              )}
              {offers?.map((offer, index) => {
                const newOffers = { ...offer, index };
                return (
                  <option
                    key={offer._id}
                    selected={index === 0}
                    value={JSON.stringify(newOffers)}
                  >
                    {offer.offerTitle}
                  </option>
                );
              })}
            </select>
          </CCardHeader>
          <CCardBody>
            <CDataTable
              items={products}
              fields={[
                "SiNo",
                { key: "name", _classes: "font-weight-bold" },
                "quantity",
                "price",
                {
                  key: "discount",
                  label: "Discount(%)",
                },
                "finalPrice",
                "remove",
              ]}
              hover
              striped
              itemsPerPage={10}
              scopedSlots={{
                SiNo: (item, index) => <td>{index + 1}</td>,
                remove: (item) => (
                  <td>
                    <CButton
                      className="text-danger"
                      onClick={() => deleteProduct(item)}
                    >
                      <DeleteForeverIcon /> Delete
                    </CButton>
                  </td>
                ),
              }}
            />
          </CCardBody>
        </CCard>
      </CCol>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </CRow>
  );
};

export default Offers;
